<template>
  <div>
    <DefaultTemplate>
      <div slot="description" class="description" v-bind:style="{marginBottom: '70px'}">
        <h1>О компании</h1>
      </div>
      <div slot="merchants" class="merchant-container">
        <div class="content">
          <h1>Удобный прием платежей</h1>
          <p>
            ArsenalPay дает возможность бизнесу любого размера принимать онлайн-оплату на сайте, а также в мобильных приложениях и социальных сетях самыми популярными способами от клиентов со всего мира.
            <br/><br/>
            Программное обеспечение ArsenalPay надежно и безопасно обрабатывает все подключения к вашей банковской карте, мобильному балансу или электронным деньгам, при покупках на сайте в Интернет или через приложения на смартфоне. Для этого мы прошли сертификацию PCI DSS – максимальный стандарт безопасности от Visa и Mastercard.
            <br/><br/>
            Мы стремимся брать на себя все заботы по приему платежей, чтобы вы могли сосредоточиться на своем бизнесе. Мы верим, что онлайн платежи могут быть понятными для бизнеса и удобными для разработчиков.
          </p>
        </div>
      </div>
    </DefaultTemplate>
  </div>
</template>

<script>
import DefaultTemplate from './DefaultTemplate';

export default {
  name: 'about-us',
  components: {
    DefaultTemplate,
  },
};
</script>



