<template>
  <div>
    <DefaultTemplate v-bind:isLoading="isLoading" v-bind:isError="isError">
      <div slot="description" v-if="!isLoading && !isError" class="description">
        <h1>{{ merchantInfo.name }}</h1>
        <p>{{ merchantInfo.paymentDescription }}</p>
      </div>
      <div slot="content">
        <div slot="merchants" class="merchant-container" v-bind:style="{marginTop: '54px'}">
          <div v-if="merchantInfo.isValid">
            <div slot="banner" class="banner" v-bind:style="{backgroundImage: `url(https://arsenalpay.ru/pay/static/images/banners/${translitMerchant(id)}.png)`}"></div>
            <div slot="merchant-description" class="merchant-description">
              <div slot="widget" class="widget">
                <div class="logotype" v-bind:style="{backgroundImage: `url(https://arsenalpay.ru/pay/static/images/logotypes/${translitMerchant(id)}.png)`}"></div>
                <div id="app-widget"></div>
              </div>
              <div slot="info" class="info">
                <h2>Информация о компании</h2>
                <p v-html="merchantInfo.description"></p>
              </div>
            </div>
          </div>
          <div v-else>
            <div class="error-404">
              <h1>404</h1>
              <p class="error-description">Уупс, мы не можем найти этот магазин</p>
              <p class="error-sub-description">Что-то пошло не так или страница больше не существует.</p>
            </div>
          </div>
        </div>
      </div>
    </DefaultTemplate>
  </div>
</template>

<script>
import axios from 'axios';
import DefaultTemplate from './DefaultTemplate';

import translitText from './../assets/__utils/translitText';

export default {
  name: 'page',
  props: {
    id: String,
  },
  data() {
    return {
      merchantInfo: {},
      isError: false,
      isLoading: true,
      APWidget: null,
      instance: axios.create({
        baseURL: 'https://arsenalpay.ru',
        responseType: 'json',
      }),
    };
  },
  methods: {
    translitMerchant(merchantName) {
      return translitText(merchantName);
    },
  },
  mounted() {
    this.instance
      .get(`/pay-api/market/merchants.php?${this.id}`)
      .then((response) => {
        this.merchantInfo = response.data;
        this.isLoading = false;
      })
      .catch(() => {
        this.isError = true;
        this.isLoading = false;
      });
  },
  updated() {
    if (window.ArsenalpayWidget && this.merchantInfo.isValid) {
      this.APWidget = new ArsenalpayWidget(
        Object.assign({}, { element: 'app-widget' }, this.merchantInfo.widget),
      );
      this.APWidget.render();
    }
  },
  beforeDestroy() {
    if (this.merchantInfo.isValid) {
      this.APWidget.destroy();
    }
  },
  components: {
    DefaultTemplate,
  },
};
</script>



