import Vue from 'vue';
import Router from 'vue-router';
import Market from '@/components/Market';
import Page from '@/components/Page';
import About from '@/components/About';
import AboutMarket from '@/components/AboutMarket';
import Contacts from '@/components/Contacts';
import Terms from '@/components/Terms';
import Payment from '@/components/Payment';

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/pay',
      name: 'market',
      component: Market,
    },
    {
      path: '/pay/:id',
      name: 'page',
      component: Page,
      props: true,
    },
    {
      path: '/pay/info/about-us',
      name: 'about-us',
      component: About,
    },
    {
      path: '/pay/info/about-service',
      name: 'about-market',
      component: AboutMarket,
    },
    {
      path: '/pay/info/contacts',
      name: 'contacts',
      component: Contacts,
    },
    {
      path: '/pay/info/terms',
      name: 'terms',
      component: Terms,
    },
    {
      path: '/pay/info/payment',
      name: 'payment',
      component: Payment,
    },
    {
      path: '*',
      redirect: '/pay',
    },
  ],
  scrollBehavior(to, from, savedPosition) {
    return { x: 0, y: 0 };
  },
});



