<template>
  <div>
    <DefaultTemplate>
      <div slot="description" class="description" v-bind:style="{marginBottom: '70px'}">
        <h1>О сервисе</h1>
      </div>
      <div slot="merchants" class="merchant-container">
        <div class="content">
          <h1>ArsenalPay.Маркет</h1>
          <p>
            ArsenalPay.Маркет — это сервис для оплаты услуг и покупки товаров.
            <br/><br/>
            Если у вашего юридического лица или ИП нет интернет магазина, то вы можете принимать платежи на вашей личной странице на ArsenalPay.Маркет. Отправляйте своим клиентам вашу именную ссылку в социальных сетях, в чате на вашем лендинг сайте, мессенджерах или по электронной почте. Покупатель по ссылке переходит на вашу страницу и оплачивает услугу или покупку удобным ему способом. Все, у кого есть ссылка, смогут в любое время оплатить ваши услуги.
            <br/><br/>
            Уведомления о каждом переводе мгновенно приходят на ваш email или в ваши системы при помощи HTTP уведомлений. Вы сразу узнаете кто и сколько денег вам оплатил. Суммы автоматически перечисляются банком на расчетный счет каждого предприятия на следующий рабочий день.
            <br/><br/>
            Напишите на <a href="mailto:pay@arsenalpay.ru">pay@arsenalpay.ru</a> и мы создадим для вас страницу.
          </p>
        </div>
      </div>
    </DefaultTemplate>
  </div>
</template>

<script>
import DefaultTemplate from './DefaultTemplate';

export default {
  name: 'about-market',
  components: {
    DefaultTemplate,
  },
};
</script>



