<template>
    <div>
      <slot name="description"></slot>

      <div class="spinner" v-if="isLoading">
        <div class="cube1"></div>
        <div class="cube2"></div>
      </div>

      <div class="error" v-if="isError">Ошибка загрузки данных...</div>

      <slot name="content" v-if="!isLoading && !isError">
        <slot name="filter"></slot>
        <slot name="merchants"></slot>
        <slot name="banner"></slot>
        <slot name="merchant-description">
          <slot name="widget"></slot>
          <slot name="info"></slot>
        </slot>
      </slot>

    </div>
</template>

<script>
export default {
  name: 'default-template',
  props: {
    isLoading: Boolean,
    isError: Boolean,
  },
};
</script>



