<template>
  <div>
    <DefaultTemplate v-bind:isLoading="isLoading" v-bind:isError="isError">
      <div slot="description" class="description">
        <h1>Оплата</h1>
        <p>Интернет, кабельное, телефон, объявления</p>
      </div>
      <div slot="content">
          <div slot="filter" class="filter">
            <ul class="list">
              <li @click="(filterMerchants('all'), setActive('all'))" v-bind:class="{active: isActive('all')}">Все</li>
              <li v-for="(category, index) in categories" :key="index" @click="(filterMerchants(category), setActive(category))" v-bind:class="{active: isActive(category)}">{{ category }}</li>
            </ul>
          </div>
        <div slot="merchants" class="merchant-container">
          <ul class="merchants">
              <router-link v-for="(merchant, index) in filteredList" :key="index" v-bind:style="[(index >= merchants.length - 3 && !filteredList.length % 2) ? {borderBottomWidth: '0px'} : '']" :to="{name: 'page', params: {id: translitMerchant(merchant.name)}}" tag="li">
                <span class="logotype" v-bind:style="{backgroundImage: `url(https://arsenalpay.ru/pay/static/images/logotypes/${translitMerchant(merchant.name)}.png)`}"></span>
                <span class="company-name"> {{ merchant.name }} </span>
              </router-link>
          </ul>
        </div>
      </div>
    </DefaultTemplate>
  </div>
</template>

<script>
import axios from 'axios';
import DefaultTemplate from './DefaultTemplate';
import translitText from './../assets/__utils/translitText';

export default {
  name: 'market',
  data() {
    return {
      merchants: [],
      categories: [],
      filteredList: [],
      active: 'all',
      isError: false,
      isLoading: true,
      instance: axios.create({
        baseURL: 'https://arsenalpay.ru',
        responseType: 'json',
      }),
    };
  },
  methods: {
    translitMerchant(merchantName) {
      return translitText(merchantName);
    },
    filterMerchants(category) {
      this.filteredList = this.merchants.filter(
        (merchant) =>
          category === 'all' ? true : merchant.categories.includes(category),
      );
    },
    setActive(item) {
      this.active = item;
    },
    isActive(item) {
      return this.active === item;
    },
  },
  mounted() {
    this.instance
      .get('/pay-api/market/merchants.php')
      .then((response) => {
        response.data.forEach((merchant) => {
          this.merchants.push(merchant);
          this.filteredList = this.merchants;
          merchant.categories.forEach((category) => {
            if (!this.categories.includes(category)) {
              this.categories.push(category);
            }
          });
        });
        this.isLoading = false;
      })
      .catch(() => {
        this.isError = true;
        this.isLoading = false;
      });
  },
  components: {
    DefaultTemplate,
  },
};
</script>



