<template>
  <div>
    <DefaultTemplate>
      <div slot="description" class="description" v-bind:style="{marginBottom: '70px'}">
        <h1>Способы оплаты</h1>
      </div>
      <div slot="merchants" class="merchant-container">
        <div class="content">
          <h1>Как оплачивать</h1>
          <p>
            На нашем сайте вы можете оплатить товар банковской картой Visa, MasterCard или Мир. После подтверждения выбранного товара или услуги откроется защищенное окно со страницей платёжного сервис провайдера ArsenalPay, где Вам необходимо ввести данные Вашей банковской карты. Для дополнительной аутентификации держателя карты используется протокол 3D Secure. Если Ваш банк поддерживает данную технологию, Вы будете перенаправлены на его сервер для дополнительной идентификации. Информацию о правилах и методах дополнительной идентификации уточняйте, пожалуйста, в банке, выдавшем Вам банковскую карту.
          </p>
          <h1 v-bind:style="{marginTop: '41px'}">Гарантии безопасности онлайн платежей</h1>
          <p>
            Процессинговый центр ArsenalPay защищает и обрабатывает данные Вашей банковской карты по максимальному международному стандарту безопасности — Payment Card Industry Data Security Standard 3.2 level 1 (сокращенно PCI DSS). Этот стандарт разработан платёжными системами Visa и MasterCard. Каждый год сертифицированные аудиторы проверяют, соответствует ли процессинговый центр всем требованиям и после этого выдают сертификат.
            Передача информации в платёжный шлюз происходит с применением технологии шифрования TLS. Дальнейшая передача информации происходит по закрытым банковским сетям, имеющим наивысший уровень надежности. ArsenalPay не хранит и не передает данные Вашей карты третьим лицам. Для дополнительной аутентификации держателя карты используется протокол 3D Secure.
            <br/><br/>
            Предоставляемая Вами персональная информация (имя, адрес, телефон, email, номер кредитной карты) является конфиденциальной и не подлежит разглашению. Данные Вашей кредитной карты передаются только в зашифрованном виде и не сохраняются на нашем сервере.
            В случае, если у Вас есть вопросы по совершенному платежу, Вы можете обратиться в службу поддержки клиентов по электронной почте <a href="mailto:pay@arsenalpay.ru">pay@arsenalpay.ru</a>.
          </p>
        </div>
      </div>
    </DefaultTemplate>
  </div>
</template>

<script>
import DefaultTemplate from './DefaultTemplate';

export default {
  name: 'payment',
  components: {
    DefaultTemplate,
  },
};
</script>



